.Navbar {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Navbar > .nav-logo {
  font-weight: 700;
  font-size: 2rem;
  margin: 15px;
  color: var(--black);
}

.Navbar > .nav-items > a {
  color: var(--orange);
  font-size: 1rem;
  text-decoration: none;
  margin: 15px;
  position: relative;
  opacity: 0.9;
  align-items: center;
  margin-right:1rem ;
}

.Navbar > .nav-items > a:hover {
  opacity: 1;
  text-shadow: 2px 1px rgba(0, 0, 0, 0.258);

}

.Navbar > .nav-items > a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: var(--black);
  transition: all 0.45s;
}

.Navbar > .nav-items > a:hover::before {
  width: 100%;
}

.nav-dark{
  margin-right: 2rem;
  width: 3rem;
  display: inline-block;
}

.Navbar > .nav-toggle {
  display: none;
}

@media (max-width: 768px) {
  .Navbar{
    margin-top: 1rem;
    display: flexbox;
    width: 100%;
  }

  .Navbar > .nav-items {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: all .45s;
    background-color: rgb(51, 74, 61);
  }
  .nav-logo{
    width: 220px;
    
}
  .Navbar > .nav-items > a::before {
      background: transparent;
  }

  .Navbar > .nav-items.open {
      transform: translateX(0);
  }

  .Navbar > .nav-toggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .nav-toggle > .bar {
    position: relative;
    width: 32px;
    height: 2px;
    background: #ffffff;
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle > .bar::before,
  .nav-toggle > .bar::after {
    content: "";
    position: absolute;
    height: 2px;
    background: #ffffff;
    border-radius: 2px;
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle > .bar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
  }

  .nav-toggle > .bar::after {
    width: 32px;
    transform: translateY(8px);
  }

  .nav-toggle.open > .bar {
      transform: translateX(-40px);
      background: transparent;
  }

  .nav-toggle.open > .bar::before {
      width: 32px;
      transform: rotate(45deg) translate(26px, -26px);
  }

  .nav-toggle.open > .bar::after {
    transform: rotate(-45deg) translate(26px, 26px);
  }
  .nav-dark{
    align-items: center;
    margin:9px;
  }


}